export const emailRegex = /^([\w-]+(?:\.[\w-]+)*)@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$/;
export const badEmail = /^\w{1,3}@\w{1,3}.\w{1,3}$/
export const emptyRegex = /^\s?$/;
export const badNames = /^([a-zA-Z]){0,}([a-zA-Z])\1{1,}([a-zA-Z]){0,}$/
export const allMoves = /^(forward|ahead|north|backward|behind|south|left|west|right|east)$/
